import React from 'react';
import { render, fireEvent, RenderAPI } from '@testing-library/react-native';
import { View, Text, TouchableOpacity } from 'react-native';
import Button from '../../src/components/Button';

describe('Button 컴포넌트', () => {
  const mockOnPress = jest.fn();
  
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('버튼이 올바르게 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="테스트 버튼" />
    );
    
    const buttonText = getByText('테스트 버튼');
    expect(buttonText).toBeDefined();
  });

  it('버튼 클릭 시 onPress 함수가 호출되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="테스트 버튼" />
    );
    
    const buttonText = getByText('테스트 버튼');
    fireEvent.press(buttonText);
    
    expect(mockOnPress).toHaveBeenCalledTimes(1);
  });

  it('비활성화된 버튼은 onPress 함수를 호출하지 않아야 함', () => {
    const { getByText, UNSAFE_getByType } = render(
      <Button onPress={mockOnPress} title="비활성화 버튼" disabled={true} />
    );
    
    // TouchableOpacity 컴포넌트를 직접 가져와서 테스트
    const touchable = UNSAFE_getByType(TouchableOpacity);
    expect(touchable.props.disabled).toBe(true);
    
    // 비활성화된 경우에는 버튼이 눌러지지 않도록 테스트 로직 수정
    const buttonText = getByText('비활성화 버튼');
    fireEvent(touchable, 'press');
    
    expect(mockOnPress).not.toHaveBeenCalled();
  });

  it('로딩 상태의 버튼은 텍스트를 숨겨야 함', () => {
    const { queryByText } = render(
      <Button onPress={mockOnPress} title="로딩 버튼" loading={true} />
    );
    
    // 로딩 중에는 텍스트가 숨겨짐
    const buttonText = queryByText('로딩 버튼');
    expect(buttonText).toBeNull();
  });

  it('primary 타입 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="프라이머리 버튼" type="primary" />
    );
    
    const buttonText = getByText('프라이머리 버튼');
    expect(buttonText).toBeDefined();
  });

  it('secondary 타입 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="세컨더리 버튼" type="secondary" />
    );
    
    const buttonText = getByText('세컨더리 버튼');
    expect(buttonText).toBeDefined();
  });

  it('outline 타입 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="아웃라인 버튼" type="outline" />
    );
    
    const buttonText = getByText('아웃라인 버튼');
    expect(buttonText).toBeDefined();
  });

  it('text 타입 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="텍스트 버튼" type="text" />
    );
    
    const buttonText = getByText('텍스트 버튼');
    expect(buttonText).toBeDefined();
  });

  it('small 크기 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="작은 버튼" size="small" />
    );
    
    const buttonText = getByText('작은 버튼');
    expect(buttonText).toBeDefined();
  });

  it('large 크기 버튼이 렌더링되어야 함', () => {
    const { getByText } = render(
      <Button onPress={mockOnPress} title="큰 버튼" size="large" />
    );
    
    const buttonText = getByText('큰 버튼');
    expect(buttonText).toBeDefined();
  });

  it('커스텀 스타일이 적용된 버튼이 렌더링되어야 함', () => {
    const customStyle = { backgroundColor: '#FF5733', borderRadius: 20 };
    
    const { getByText } = render(
      <Button 
        onPress={mockOnPress} 
        title="커스텀 스타일 버튼" 
        style={customStyle} 
      />
    );
    
    const buttonText = getByText('커스텀 스타일 버튼');
    expect(buttonText).toBeDefined();
  });

  it('커스텀 텍스트 스타일이 적용된 버튼이 렌더링되어야 함', () => {
    // 인라인으로 스타일 객체를 직접 전달
    const { getByText } = render(
      <Button 
        onPress={mockOnPress} 
        title="커스텀 텍스트 스타일" 
        textStyle={{ fontSize: 18, fontWeight: 'bold', color: '#FF0000' }}
      />
    );
    
    const buttonText = getByText('커스텀 텍스트 스타일');
    expect(buttonText).toBeDefined();
  });

  it('leftIcon이 있는 버튼이 올바르게 렌더링되어야 함', () => {
    const leftIcon = <View testID="left-icon" />;
    
    const { getByTestId, getByText } = render(
      <Button 
        onPress={mockOnPress} 
        title="왼쪽 아이콘 버튼" 
        leftIcon={leftIcon}
      />
    );
    
    // 아이콘이 렌더링되었는지 확인
    const icon = getByTestId('left-icon');
    expect(icon).toBeDefined();
    
    // 텍스트도 렌더링되었는지 확인
    const buttonText = getByText('왼쪽 아이콘 버튼');
    expect(buttonText).toBeDefined();
  });

  it('rightIcon이 있는 버튼이 올바르게 렌더링되어야 함', () => {
    const rightIcon = <View testID="right-icon" />;
    
    const { getByTestId, getByText } = render(
      <Button 
        onPress={mockOnPress} 
        title="오른쪽 아이콘 버튼" 
        rightIcon={rightIcon}
      />
    );
    
    // 아이콘이 렌더링되었는지 확인
    const icon = getByTestId('right-icon');
    expect(icon).toBeDefined();
    
    // 텍스트도 렌더링되었는지 확인
    const buttonText = getByText('오른쪽 아이콘 버튼');
    expect(buttonText).toBeDefined();
  });

  it('로딩 상태에서는 아이콘이 표시되지 않아야 함', () => {
    const leftIcon = <View testID="left-icon" />;
    const rightIcon = <View testID="right-icon" />;
    
    const { queryByTestId } = render(
      <Button 
        onPress={mockOnPress} 
        title="로딩 상태 아이콘 버튼" 
        leftIcon={leftIcon}
        rightIcon={rightIcon}
        loading={true}
      />
    );
    
    // 로딩 중에는 아이콘이 표시되지 않아야 함
    const leftIconElement = queryByTestId('left-icon');
    const rightIconElement = queryByTestId('right-icon');
    expect(leftIconElement).toBeNull();
    expect(rightIconElement).toBeNull();
  });
});